import React, { Component } from "react";
import {
  CBadge,
  CButton,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CRow,
  CSelect,
} from "@coreui/react";
import { Link } from "react-router-dom";
import api from "../../api";
import authHeader from "../../auth-header";
import { MDBDataTableV5 } from "mdbreact";
import Toast from "light-toast";
import swal from "sweetalert";
import { ScaleLoader } from "react-spinners";
import { css } from "@emotion/core";
const override = css`
  display: block;
  border-color: black;
  position: absolute;
  left: 43%;
  top: 50%;
  z-index: 1;
`;

class serviceList extends Component {
  constructor(props) {
    super(props);
    this.serviceType = this.serviceType.bind(this);
    this.serviceStatus = this.serviceStatus.bind(this);
    this.serviceAction = this.serviceAction.bind(this);
    // this.serviceEdit = this.serviceEdit.bind(this);
    this.serviceRemove = this.serviceRemove.bind(this);

    this.filter = this.filter.bind(this);

    this.state = {
      data: [],
      loading: false,
      serviceTypeFilter: "",
      statusTypeFilter: "",

      serviceId: "null",
      serviceType: "",
    };
  }

  serviceIcon = (img) => {
    if (img !== "") {
      return (
        <img
          src={api.imgUrlService + img}
          style={{ width: "70px", height: "70px" }}
        ></img>
      );
    } else {
      return (
        <img src={api.noImage} style={{ width: "70px", height: "70px" }}></img>
      );
    }
  };

  serviceType = (id) => {
    if (id === 0) {
      return (
        <CBadge className="mr-1" color="primary">
          Garage
        </CBadge>
      );
    } else if (id === 1) {
      return (
        <CBadge className="mr-1" color="secondary">
          Spares
        </CBadge>
      );
    } else if (id === 2) {
      return (
        <CBadge className="mr-1" color="info">
          Ancillary
        </CBadge>
      );
    }
  };

  serviceStatus = (id) => {
    if (id === 0) {
      return (
        <CBadge className="mr-1" color="danger">
          Inactive
        </CBadge>
      );
    } else if (id === 1) {
      return (
        <CBadge className="mr-1" color="success">
          Active
        </CBadge>
      );
    }
  };

  serviceAction = (id, val) => {
    return (
      <div style={{ width: "108px" }}>
        {/* <button class="btn submit-btn" onClick={() => this.serviceEdit(id)}>
          <i className="fa fa-edit"></i>
        </button> */}
        <Link
          className="btn submit-btn"
          to={{
            pathname: "/edit-service",
            data: id, // your data array of objects
          }}
        >
          <i className="fa fa-edit"></i>
        </Link>{" "}
        <button
          className="btn cancel-btn"
          onClick={() => this.serviceRemove(id, val)}
        >
          {val === 1 ? (
            <i className="fa fa-remove"></i>
          ) : (
            <i className="fa fa-check"></i>
          )}
        </button>
      </div>
    );
  };

  // serviceEdit = (id) => {
  //   alert(id);
  // };

  serviceRemove = (id, status) => {
    var data = {
      id: id,
      status: status,
    };

    // this.setState({ loading: true });
    swal({
      title: "Are you sure?",
      text: "Service status will changed!",
      icon: "warning",
      buttons: true,
      dangerMode: true,
    }).then((willDelete) => {
      if (willDelete) {
        swal("Status updated successfully!", {
          icon: "success",
        });
        fetch(api.apiUrl + "delete_service", {
          method: "POST",
          headers: authHeader(),
          body: JSON.stringify(data),
        })
          .then((response) => response.json())
          .then((responseJsonFromServerData) => {
            console.log(responseJsonFromServerData);
            if (responseJsonFromServerData === "success") {
              this.componentDidMount();
            } else {
              this.props.history.push("/404");
            }
          })
          .catch((error) => {
            console.error(error);
            this.props.history.push("/404");
          });
      } else {
        // swal("Your imaginary file is safe!");
      }
    });
  };

  filter = (val, type) => {
    var info = [];

    this.setState({
      serviceId: val,
      serviceType: type,
    });

    if (type === "service") {
      this.setState({ serviceTypeFilter: val });
      var data = {
        service_id: val,
        status:
          this.state.statusTypeFilter !== ""
            ? this.state.statusTypeFilter
            : "null",
      };
    }

    if (type === "status") {
      this.setState({ statusTypeFilter: val });
      var data = {
        service_id:
          this.state.serviceTypeFilter !== ""
            ? this.state.serviceTypeFilter
            : "null",
        status: val,
      };
    }
    this.setState({ loading: true });
    fetch(api.apiUrl + "fetch_service", {
      method: "POST",
      headers: authHeader(),
      body: JSON.stringify(data),
    })
      .then((response) => response.json())
      .then((responseJsonFromServerData) => {
        console.log(responseJsonFromServerData);
        if (responseJsonFromServerData.length !== 0) {
          responseJsonFromServerData.map((item, key) => {
            var arr = {
              icon: this.serviceIcon(item.icon),
              code: item.code,
              name: item.name,
              serviceType: this.serviceType(item.serviceType),
              status: this.serviceStatus(item.status),
              action: this.serviceAction(item.id, item.status),
            };
            info.push(arr);
          });
          this.setState({
            data: info,
            loading: false,
          });
        } else {
          this.setState({ loading: false });
          Toast.info("No Data Found!!", 2000, () => {
            // do something after the toast disappears
          });
        }
      })
      .catch((error) => {
        console.error(error);
        this.setState({ loading: false });
        this.props.history.push("/404");
      });
  };

  componentDidMount() {
    var info = [];
    this.setState({ loading: true });
    fetch(api.apiUrl + "get_services", {
      method: "POST",
      headers: authHeader(),
      // body: JSON.stringify(profile_data),
    })
      .then((response) => response.json())
      .then((responseJsonFromServerData) => {
        console.log(responseJsonFromServerData);
        if (responseJsonFromServerData.length !== 0) {
          responseJsonFromServerData.map((item, key) => {
            var arr = {
              icon: this.serviceIcon(item.icon),
              code: item.code,
              name: item.name,
              serviceType: this.serviceType(item.serviceType),
              status: this.serviceStatus(item.status),
              action: this.serviceAction(item.id, item.status),
            };
            info.push(arr);
          });
          this.setState({
            data: info,
            loading: false,
          });
        } else {
          //redirect to 404 page
          this.props.history.push("/404");
        }
      })
      .catch((error) => {
        console.error(error);
        this.setState({ loading: false });
      });
  }

  render() {
    var datatable = {
      columns: [
        {
          label: "Icon",
          field: "icon",
          sort: "disabled",
          width: 150,
          attributes: {
            "aria-controls": "DataTable",
            "aria-label": "Icon",
          },
        },
        {
          label: "Code",
          field: "code",
          sort: "disabled",
          width: 270,
        },
        {
          label: "Name",
          field: "name",
          sort: "disabled",
          width: 200,
        },
        {
          label: "Service Type",
          field: "serviceType",
          sort: "disabled",
          width: 100,
        },
        {
          label: "Status",
          field: "status",
          sort: "disabled",
          width: 150,
        },
        {
          label: "Action",
          field: "action",
          sort: "disabled",
          width: 270,
        },
      ],
      rows: [...this.state.data],
    };
    return (
      <>
        <CRow>
          <CCol>
            <CCard>
              <ScaleLoader
                height={40}
                width={12}
                css={override}
                size={150}
                //size={"150px"} this also works
                color={"#B54545"}
                loading={this.state.loading}
              />
              <CCardHeader>
                <CRow>
                  <CCol sm="6" xs="6" md="8">
                    <h3>Services</h3>
                  </CCol>
                  <CCol sm="6" xs="6" md="4">
                    <CButton
                      className="submit-btn"
                      to="/add-service"
                      style={{ float: "right" }}
                    >
                      Add
                    </CButton>
                  </CCol>
                </CRow>
              </CCardHeader>
              <CCardBody>
                <CRow>
                  <CCol md="3" sm="3" xs="12">
                    <CSelect
                      custom
                      name="ccmonth"
                      id="ccmonth"
                      onChange={(e) => this.filter(e.target.value, "service")}
                      defaultValue={this.state.serviceTypeFilter}
                      style={{ margin: "5px" }}
                    >
                      <option value="null">Select Service</option>
                      <option value="0">Garage</option>
                      <option value="1">Spares</option>
                      <option value="2">Ancillary</option>
                    </CSelect>
                  </CCol>

                  <CCol md="3" sm="3" xs="12">
                    <CSelect
                      custom
                      name=""
                      id=""
                      onChange={(e) => this.filter(e.target.value, "status")}
                      defaultValue={this.state.statusTypeFilter}
                      style={{ margin: "5px" }}
                    >
                      <option value="null">Select Status</option>
                      <option value="1">Active</option>
                      <option value="0">Inactive</option>
                    </CSelect>
                  </CCol>
                </CRow>
                <CRow>
                  <CCol md="12" sm="12" xs="12">
                    <MDBDataTableV5
                      hover
                      entriesOptions={[6, 10]}
                      entries={6}
                      pagesAmount={4}
                      data={datatable}
                      pagingTop
                      searchTop
                      searchBottom={false}
                      barReverse
                      responsive
                    />
                  </CCol>
                </CRow>
              </CCardBody>
            </CCard>
          </CCol>
        </CRow>
      </>
    );
  }
}
export default serviceList;
